import React from 'react';
import { useSelector } from 'react-redux';
// material-ui
import { makeStyles } from '@material-ui/styles';
import { Box, Button, Grid, FormControl, FormHelperText, OutlinedInput, CircularProgress } from '@material-ui/core';
// third party
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useHistory } from 'react-router';

// project imports
import AnimateButton from '../../../ui-component/extended/AnimateButton';
import { FormProvider, RHFTextField } from '../../../components/hook-form';
import { useForm } from 'react-hook-form';
import { useDispatch } from 'react-redux';
import { registerUserByOtp } from '../../../store/actions/AuthActions/Register.actions';

// style constant
const useStyles = makeStyles((theme) => ({
    loginInput: {
        ...theme.typography.customInput
    }
}));

//============================|| Login with mobile OTP ||============================//

const SendOtp = (props, { ...others }) => {
    const classes = useStyles();
    const history = useHistory();
    const dispatch = useDispatch();
    const { loading } = useSelector((state) => state.login);

    const OtpSchema = Yup.object().shape({
        mobile: Yup.string().required('Mobile is required')
    });

    const defaultValues = {
        mobile: ''
    };
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(OtpSchema),
        defaultValues
    });
    const { handleSubmit } = methods;
    const onSubmit = (data) => {
        const payload = {
            mobile: data?.mobile
        };
        dispatch(
            registerUserByOtp(payload, () => {
                history.push({ pathname: '/verify-otp', state: { phoneNumber: data?.mobile } });
            })
        );
    };

    return (
        <>
            <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                <Grid container spacing={2}>
                    <Grid item xs={12}>
                        <RHFTextField name="mobile" placeholder="Enter your mobile number" autoFocus />
                    </Grid>
                    <Grid item xs={12}>
                        <Button disableElevation fullWidth size="large" type="submit" variant="contained" color="primary">
                            {loading ? <CircularProgress color="primary" size={25} /> : 'Send otp'}
                        </Button>
                    </Grid>
                </Grid>
            </FormProvider>
        </>
    );
};

export default SendOtp;
