import { http } from '../../../utils/http';
import {
    REGISTER_USER_START,
    REGISTER_USER_SUCCESS,
    REGISTER_USER_ERROR,
    REGISTER_USER_OTP_START,
    REGISTER_USER_OTP_SUCCESS,
    REGISTER_USER_OTP_ERROR
} from '../../constants';
import { setMessage } from '../MessageActions';

const registerStart = () => {
    return { type: REGISTER_USER_START };
};
const registerSuccess = (payload) => {
    return { type: REGISTER_USER_SUCCESS, payload };
};
const registerError = (error) => {
    return { type: REGISTER_USER_ERROR, error };
};

const registerOtpStart = () => {
    return { type: REGISTER_USER_OTP_START };
};
const registerOtpSuccess = (payload) => {
    return { type: REGISTER_USER_OTP_SUCCESS, payload };
};
const registerOtpError = (error) => {
    return { type: REGISTER_USER_OTP_ERROR, error };
};

const registerUserByEmail = (payload, successCB) => (dispatch) => {
    dispatch(registerStart());
    return http(process.env.REACT_APP_ROOT_URL, 'POST', `users-service/users`, payload, false)
        .then((res) => {
            const payload = {
                data: res
            };
            dispatch(registerSuccess(payload));
            dispatch(setMessage(res?.message));
            successCB && successCB(res);
            return res;
        })
        .catch((err) => {
            dispatch(registerError(err));
            dispatch(setMessage(err?.data?.message));
            throw err;
        });
};

const registerUserByOtp = (payload, successCb) => (dispatch) => {
    dispatch(registerOtpStart());
    http(process.env.REACT_APP_ROOT_URL, 'POST', `users-service/users/mobile/otp`, payload, false)
        .then((res) => {
            dispatch(registerOtpSuccess(payload));
            dispatch(setMessage(res.message));
            successCb && successCb(res);
        })
        .catch((err) => {
            dispatch(registerOtpError(err));
            // dispatch(setMessage(err.error));
            dispatch(setMessage(err?.data?.message));
        });
};

export { registerUserByEmail, registerUserByOtp };
